<template><section><h2>Toast 轻提示</h2>
<p>Toast 组件会自动在 Vue 的 prototype 上挂载 $toast 方法，便于在组件内调用。</p>
<h3>基础用法</h3>
<div class="demo-block">
  <tj-button round="3px" @click="handleTip">文字提示</tj-button>
  <tj-button class="ml_10" @click="handleLongTip" round="3px">长文字提示</tj-button>
  
</div>
<demo-block>
                    <div slot="desc">  <tj-button round="3px" @click="handleTip">文字提示</tj-button>
  <tj-button class="ml_10" @click="handleLongTip" round="3px">长文字提示</tj-button>

  
</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleTip"</span>&gt;</span>文字提示<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleLongTip"</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span>&gt;</span>长文字提示<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        handleTip() {
          <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'文字提示'</span>)
        },
        handleLongTip() {
          <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'这是一条长文字提示，超过一定字数就会换行'</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>参数传递</h3>
<div class="demo-block">
  <tj-button round="3px" @click="handleToast">文字提示</tj-button>
  <tj-button class="ml_10" @click="handleToast2" round="3px">长文字提示</tj-button>
</div>
<demo-block>
                    <div slot="desc"><tj-button round="3px" @click="handleToast">文字提示</tj-button>
<tj-button class="ml_10" @click="handleToast2" round="3px">长文字提示</tj-button>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleToast"</span>&gt;</span>文字提示<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleToast2"</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span>&gt;</span>长文字提示<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      handleToast() {
        <span class="hljs-keyword">this</span>.$toast({
          <span class="hljs-attr">message</span>: <span class="hljs-string">'操作成功'</span>,
          <span class="hljs-attr">iconClass</span>: <span class="hljs-string">'tj-icon tj-success_no_circle'</span>
        })
      },
      handleToast2() {
        <span class="hljs-keyword">this</span>.$toast({
          <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一条长文字提示，超过一定字数就会换行'</span>,
          <span class="hljs-attr">position</span>: <span class="hljs-string">'bottom'</span>
        })
      },
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>文本内容</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>position</td>
<td>位置</td>
<td>string</td>
<td>top, bottom, middle</td>
<td>middle</td>
</tr>
<tr>
<td>duration</td>
<td>持续时间（毫秒），若为 -1 则不会自动关闭</td>
<td>number</td>
<td></td>
<td>3000</td>
</tr>
<tr>
<td>className</td>
<td>给容器添加的类名</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>iconClass</td>
<td>icon 图标的类名</td>
<td>string</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      methods: {
        handleTip() {
          this.$toast('文字提示')
        },
        handleLongTip() {
          this.$toast('这是一条长文字提示，超过一定字数就会换行')
        },
        handleToast() {
          this.$toast({
            message: '操作成功',
            iconClass: 'tj-icon tj-success_no_circle'
          })
        },
        handleToast2() {
          this.$toast({
            message: '这是一条长文字提示，超过一定字数就会换行',
            position: 'bottom'
          })
        },
      }
    }
  </script>


